<template>
  <div>
    <appbar></appbar>
    <div class="post-wrapper">
      <mu-row>
        <mu-col width="0" tablet="15" desktop="25"></mu-col>
        <mu-col width="100" tablet="70" desktop="50">
          <mu-content-block>
            <div v-highlight v-html="body"></div>
          </mu-content-block>
          <div class="comment-wrapper">
            <mu-list>
              <div v-for="comment in comments" :key=comment.id>
                <mu-list-item :title="comment.author_name">
                  <mu-avatar :src="comment.author_avatar" slot="leftAvatar"/>
                  <span slot="describe">
                    <span style="color: rgba(0, 0, 0, .87)">{{ comment.created_time | moment }}</span>
                  </span>
                  <p>{{ comment.body }}</p>
                </mu-list-item>
                <mu-divider inset/>
              </div>
            </mu-list>
            <mu-text-field v-model="commentBody" hintText="评论" multiLine :rows="3" fullWidth /><br/>
            <mu-raised-button v-show="login" label="提交" class="demo-raised-button" @click="submitForm"/>
            <mu-raised-button v-show="!login" label="登录后才能评论" class="demo-raised-button" @click="toLogin"/>
          </div>
        </mu-col>
        <mu-col width="0" tablet="15" desktop="25"></mu-col>
      </mu-row>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import appbar from '@/components/Appbar'

export default {
  data () {
    return {
      value: '1',
      avatar: window.sessionStorage.getItem('avatar'),
      login: window.sessionStorage.getItem('key'),
      body: '',
      comments: '',
      commentBody: ''
    }
  },
  methods: {
    logout () {
      let _this = this
      _this.login = false
      window.sessionStorage.removeItem('key')
      window.sessionStorage.removeItem('admin')
      window.sessionStorage.removeItem('avatar')
      axios({
        method: 'DELETE',
        url: '/sessions',
        headers: {
          'Content-Type': 'application/json'
        }
      }).then(function (response) {
        _this.$router.push({'name': 'HelloWorld'})
      }).catch(function (error) {
        console.log(error)
      })
    },
    toLogin () {
      this.$router.push({name: 'Login'})
    },
    handleChange (val) {
      if (this.value === val) {
        this.logout()
      }
    },
    fetchPost (id) {
      let _this = this
      axios({
        method: 'GET',
        url: 'posts/' + id,
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        _this.body = response.data.body
      }).catch((error) => {
        console.log(error)
      })
    },
    fetchComment (id) {
      let _this = this
      axios({
        method: 'GET',
        url: 'comments/' + id,
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        _this.comments = response.data
      }).catch((error) => {
        console.log(error)
      })
    },
    fetchData (id) {
      this.fetchPost(id)
      this.fetchComment(id)
    },
    submitForm () {
      let _this = this
      if (!_this.commentBody) {
        alert('评论不能为空')
        return
      }
      axios({
        url: '/comments/' + _this.$route.params.id,
        method: 'POST',
        headers: {'Authorization': window.sessionStorage.getItem('key'), 'Content-Type': 'application/json'},
        data: {
          'body': _this.commentBody
        }
      }).then((response) => {
        this.fetchComment(this.$route.params.id)
      }).catch((error) => {
        console.log(error)
      })
    }
  },
  mounted () {
    this.fetchData(this.$route.params.id)
  },
  components: {
    appbar
  },
  watch: {
    '$route' (to, from) {
      // 获取最新的id 调用获取数据方法
      this.fetchData(this.$route.params.id)
    }
  }
}
</script>

<style>
  .post-wrapper {
    margin-top: 40px;
  }
</style>



