<template>
  <div>
    <mu-appbar title="Adminstrator">
      <mu-flat-button color="white" label="登出" slot="right" @click="logout"/>
    </mu-appbar>
    <mu-row class="container">
      <mu-col desktop="10"></mu-col>
      <mu-col desktop="10">
        <mu-list>
         <mu-list-item title="文章数">
            <mu-badge :content="postNums" slot="after"/>
          </mu-list-item>
          <mu-list-item title="用户数">
            <mu-badge :content="usertNums" slot="after"/>
          </mu-list-item>
        </mu-list>
      </mu-col>
      <mu-col desktop="70">
        <mu-table :selectable="false">
          <mu-thead>
            <mu-tr>
              <mu-th>ID</mu-th>
              <mu-th>Title</mu-th>
              <mu-th>Operation</mu-th>
            </mu-tr>
          </mu-thead>
          <mu-tbody>
            <mu-tr v-for="p in posts" :key="p.id">
              <mu-td>{{ p.id }}</mu-td>
              <mu-td>{{ p.title }}</mu-td>
              <mu-td>
                <mu-raised-button label="修改" @click="editPost(p.id)"/>
                <mu-raised-button label="删除" @click="deletePost(p.id)" secondary/>
              </mu-td>
            </mu-tr>
          </mu-tbody>
        </mu-table>
      </mu-col>
      <mu-col desktop="10">
        <div class="new-button">
          <mu-float-button icon="add" class="demo-float-button" slot="right" @click="addPost"/>
        </div>
      </mu-col>
    </mu-row>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      posts: [],
      postNums: '',
      usertNums: ''
    }
  },
  methods: {
    logout () {
      let _this = this
      window.sessionStorage.removeItem('key')
      window.sessionStorage.removeItem('admin')
      window.sessionStorage.removeItem('avatar')
      axios({
        method: 'DELETE',
        url: '/sessions',
        headers: {
          'Content-Type': 'application/json'
        }
      }).then(function (response) {
        _this.$router.push({'name': 'HelloWorld'})
      }).catch(function (error) {
        console.log(error)
      })
    },
    fetchUsers () {
      let _this = this
      axios({
        method: 'GET',
        url: 'users',
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        _this.usertNums = response.data.amount
      }).catch((error) => {
        console.log(error)
      })
    },
    fetchPosts () {
      let _this = this
      axios({
        method: 'GET',
        url: 'posts',
        headers: {
          'Content-Type': 'application/json'
        }
      }).then((response) => {
        _this.posts = response.data.posts
      }).catch((error) => {
        console.log(error)
      })
    },
    fetchData () {
      this.fetchUsers()
      this.fetchPosts()
    },
    addPost () {
      this.$router.push({'name': 'Add'})
    },
    deletePost (id) {
      var _this = this
      axios({
        method: 'DELETE',
        url: '/posts/' + id,
        headers: {
          'Content-Type': 'application/json'
        }
      }).then(function (response) {
        _this.fetchData()
      }).catch(function (error) {
        console.log(error)
      })
    },
    editPost (postId) {
      this.$router.push({name: 'Edit', params: {id: postId}})
    }
  },
  mounted () {
    this.fetchData()
  }
}
</script>

<style>
  .container {
    margin-top: 40px;
  }
  .new-button {
    float: left;
  }
</style>





